import uniPopup from "../../components/uni-ui/uni-popup/uni-popup.vue"
export default {
name: "",
components: {
	uniPopup
},
props: {},
data () {
  return {
	  userId: "",
	  faceImage: "",
	  nickname: "",
	  birthday: "",
	  sex: "",
	  userUniqueToken: ""
    }
  },
  methods: {
	// 打开弹出层
	openPop() {
		this.$refs.popup.open()
	},
	// 查看头像
	previewImage() {
		let arr = []
		arr.push(this.faceImage)
		uni.previewImage({
			urls: arr
		});
	},
	// 从本地相册选择图片
	chooseImage() {
		let that = this
		uni.chooseImage({
		    count: 1, //默认9
		    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
		    sourceType: ['album'], //从相册选择
		    success: function (res) {
				const tempFilePaths = res.tempFilePaths;
				uni.uploadFile({
				    url: `${that.$api}/user/uploadFace?qq=122212489&userId=${that.userId}`, 
				    filePath: tempFilePaths[0],
				    name: 'file',
				    header: {
				        'headerUserId': that.userId,
						"headerUserToken": that.userUniqueToken
				    },
				    success: (uploadFileRes) => {
						let user = JSON.parse(uploadFileRes.data)
						that.faceImage = user.data.faceImage
				        console.log(JSON.parse(uploadFileRes.data));
				    }
				});
		    }
		});
	},
	// 关闭弹出层
	closePop() {
		this.$refs.popup.close()
	},
	// 去修改昵称
	goNickname() {
		uni.navigateTo({
			url: `/pages/setname/setname?name=${this.nickname}`,
			success: res => {},
			fail: () => {},
			complete: () => {}
		});
	},
	// 去修改生日
	goBirthday() {
		uni.navigateTo({
			url: `/pages/setbirthday/setbirthday?birthday=${this.birthday}`,
			success: res => {},
			fail: () => {},
			complete: () => {}
		});
	},
	// 去修改性别
	goSex() {
		uni.navigateTo({
			url: `/pages/setsex/setsex?sex=${this.sex}`,
			success: res => {},
			fail: () => {},
			complete: () => {}
		});
	},
	// 清除缓存
	clear() {
		uni.removeStorageSync('userInfo');
		this.$store.state.username = ''
		uni.showToast({
		    title: '清除成功',
		    duration: 2000
		});
	},
	// 退出登录
	quit() {
		uni.request({
			url: `${this.$api}/user/logout?qq=122212489&userId=${this.userId}`,
			method: 'POST',
			data: {},
			success: res => {
				uni.switchTab({
					url: '/pages/mine/mine'
				})
				uni.removeStorageSync('username')
				console.log(res, '退出登录');
			},
			fail: () => {},
			complete: () => {}
		});
	}
  },
  mounted () {

  },
  onLoad () {
	
  },
  onShow() {
  	let user = uni.getStorageSync('userInfo');
  	this.userId = user.id;
  	this.faceImage = user.faceImage
  	this.nickname = user.nickname
  	this.birthday = user.birthday
  	this.sex = user.sex,
  	this.userUniqueToken = user.userUniqueToken
  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
